import React from 'react';

class ModeSelector extends React.Component {
	render () {
		return (
			<ul className="modeSelector">
				{this.props.modes.map((m) => <li key={m} className={this.props.mode === m ? 'active' : ''}>{m}</li>)}
			</ul>
		);
	}
};

export default ModeSelector






